<template>
    <div class="playpage">
        <div class="top">
            <img class="bg"   src="../images/f-day_bg_question.png">
            <p class="desc">{{desc}}</p>
        </div>
        <div  id='wan' @click="begin" class="play moveBottom">
            <transition-group  name="slide-fade" >
                <div class="cont" v-for="(li,key) in timu "  :key="li" ><span>{{li}}</span></div>
            </transition-group>
        </div>
        <p  class="bottom">{{show_page}}/7</p>
        <div id="b"></div>
    </div>
</template>

<script>
    let fuckyou = true
    let diyici = true

    function randomNum(minNum,maxNum){
        switch(arguments.length){
            case 1:
                return parseInt(Math.random()*minNum+1,10);
                break;
            case 2:
                return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10);
                break;
            default:
                return 0;
                break;
        }
    }
    export default {
        name: "PlayPage",
        data(){
            return {
                desc: '1.',
                timu: ['A.', 'B.'],
                page: 1,
                show_page:1,
                man_timu_list: ['2.','3.', '4.', '5.', '6.', '7.' ],
                man: [{
                    0: 'A.',
                    5:'B.',
                    25:'C.',
                    15:'D.'
                },{
                    1:'A.',
                    5:'B.',
                    20:'C./',
                    30:'D.'
                },{
                    0:'A.',
                    10:'B.',
                    25:'C.',
                    6:'D.'
                },{
                    40:'A.',
                    5:'B.',
                    10:'C.',
                    20:'D.'
                },{
                    1:'A.',
                    10:'B.',
                    5:'C.',
                    20:'D.'
                },{
                    20:'A./',
                    10:'B./',
                    5:'C./',
                    1:'D./'
                }],
                woman_timu_list: ['2./', '3.', '4.', '5.', '6.', '7.'],
                woman:[{
                    10:'A.',
                    25:'B./',
                    0:'C.',
                    15:'D./'
                },{
                    15:'A.',
                    0:'B.',
                    5:'C.',
                    30:'D.'
                },{
                    3:'A.',
                    0:'B.',
                    5:'C.',
                    20:'D.'
                },{
                    5:'A.',
                    10:'B.',
                    0:'C.',
                    15:'D.'
                },{
                    10:'A.Superman',
                    5:'B.',
                    0:'C.',
                    25:'D.'
                },{
                    5:'A.',
                    10:'B.',
                    20:'C.',
                    0:'D'
                }],
                cont_timu:[],
                cont_desc:[],
                score:0,
                items:[1,2,3,4]
            }
        },
        methods:{
            test(){
               // const keys = Object.keys( this.timu )
               // console.log(keys)
               // Vue.delete(this.timu,keys[0])
                this.timu.pop()



                if(! (0 in this.timu) ) {
                    setTimeout(
                        ()=>{

                            const li = []
                            const keys = []

                            try{
                                Object.values( this.cont_timu[this.page-2] ).find((n)=>{
                                    if(n.split('').includes('A')){
                                        li[0] = n
                                        keys[0] = _.findKey(this.cont_timu[this.page-2], function(o) { return o == n  })
                                    }else if(n.split('').includes('B')){
                                        li[1] = n
                                        keys[1] = _.findKey(this.cont_timu[this.page-2], function(o) { return o == n  })
                                    }else if(n.split('').includes('C')){
                                        li[2] = n
                                        keys[2] = _.findKey(this.cont_timu[this.page-2], function(o) { return o == n  })
                                    }else if(n.split('').includes('D')){
                                        li[3] = n
                                        keys[3] = _.findKey(this.cont_timu[this.page-2], function(o) { return o == n })
                                    }
                                })

                            }catch(e){
                                localStorage.setItem('score',randomNum(0,100))
                                this.$router.push({path:'/result'})

                            }

                            console.log(keys)
                            console.log('')
                            this.timu = li


                            this.desc = this.cont_desc[this.page-2]
                            console.log('')
                            setTimeout(()=>{
                                for( let i = 0 ;i<4;i++ ){
                                    try{
                                        document.querySelectorAll(".cont")[i].setAttribute('score',keys[i])
                                        console.log('sb?')
                                    }catch(e){

                                    }

                                    //li.setAttribute('key',this.cont_timu[this.page-2][index])
                                }
                            },500)
                            fuckyou =true

                        }

                    ,500)
                }
            },
            next(e){

                if( !e.target.firstChild.firstChild ){ //

                    setTimeout(()=>{
                        if(this.show_page < 7){
                            this.show_page += 1
                        }

                    },400) //
                    console.log(e.target.parentNode)

                    if(e.target.parentNode.getAttribute('score')){
                        console.log(   e.target.parentNode.getAttribute('score') +'')
                        this.score += parseInt( e.target.parentNode.getAttribute('score') )
                    }

                    this.fade()
                }else if(  e.target.classList[0] == 'cont'  ){
                    setTimeout(()=>{
                        if(this.show_page < 7){
                            this.show_page += 1
                        }
                    },400) // 
                    console.log(e.target)

                    if(e.target.getAttribute('score')){
                        console.log(   e.target.getAttribute('score') +'')
                        this.score += parseInt( e.target.getAttribute('score') )
                    }
                    this.fade()
                }

            },
            fade(){
                let count = 0
                const inter = setInterval(
                    ()=>{
                        this.$nextTick(()=>{
                            this.test()
                            count += 1
                            let max = 4
                            if(diyici === true){
                                max = 2
                            }
                            if(count === max){
                                diyici = false
                                clearInterval(inter)
                            }
                        })
                    },0)
            },
            begin(e){
                e.preventDefault()
                if(fuckyou) {
                    if( !e.target.firstChild.firstChild ){
                        e.target.parentNode.classList.add('bg-change')
                        fuckyou = false
                    }else if(  e.target.classList[0] == 'cont'  ){
                        e.target.classList.add('bg-change')
                        fuckyou = false
                    }

                    this.$nextTick(()=>{
                        setTimeout(()=>{
                            this.debounce(e, this)
                        },200)

                    })

                }
            },
            sexChange(sex){
                const string = sex
                if( sex == 'A.'){
                    localStorage.setItem('sex','man')
                    this.cont_desc = this.man_timu_list  //timu 
                    this.cont_timu = this.man
                }else{
                    localStorage.setItem('sex','woman')
                    this.cont_desc = this.woman_timu_list
                    this.cont_timu = this.woman
                }
            },
            debounce:_.debounce((e, that)=>{
                if(1 in document.querySelector('#wan').classList){

                    //AB
                    if( !e.target.firstChild.firstChild ){ //
                        console.log( e.target.innerHTML)
                        that.sexChange(e.target.innerHTML)
                    }else if(  e.target.classList[0] == 'cont'  ){
                        console.log( e.target.firstChild.firstChild +'asdasdasdasdads')
                        that.sexChange(e.target.firstChild.innerHTML)
                    }
                    //
                    setTimeout(()=>{
                        document.querySelector('#wan').classList.remove('moveBottom')
                    },500)
                    that.$data.page += 1
                    that.next(e)
                }else{


                    if(!(that.page == 8)){
                        that.page += 1
                        that.next(e)
                    }
                    if(that.page == 8){
                        that.$nextTick(()=>{
                            //that.page -= 2
                            console.log(that.score+'')
                            const score = that.score
                            //that.fade()
                            localStorage.setItem('score',score)
                            setTimeout(()=>{
                                that.$router.push({path:'/result'})
                            },700)

                        })
                    }
                }
            },100, {leading : true})
        },
        mounted(){
            fuckyou = true
            diyici = true
        }

    }
</script>

